.realizations {
  font-family: 'Rajdhani', sans-serif;
  font-weight: 500;
  margin: 0 auto 20px auto;
  padding-top: 10px;
  width: 80%;
  min-height: 500px;
  max-width: 1600px;
  display: none;
  flex-direction: column;
  justify-content: left;
  background-color: rgba(255, 255, 255, 0.07);
  border-radius: 30px;

  .realizationsMain{
    margin: 0 auto;
    width: 90%;
    height: 80%;

    h3{
      margin-top: 10px;
      color: #faaf47;
      font-weight: 600;
      font-size: 30px;
    }

    .realizationsGalleries{
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;


    }
  }
}
@media (max-width: 470px) {
  .realizations {
    width: 90%;
  }
}